import React from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import {Link} from 'react-router-dom';
import './Products.css';
import formatCurrency from '../Currency';

export default function Products (props){
    const { product } = props;

        return (
            <div className="single-card" key={product._id}>
            <Link to={`/product/${product._id}`}><img src={product.Image} className="card-img-top product-image"/></Link>
            <div className="card-body">
            <h5 className="card-title"><span className='single-product-title'>
            <Link to={`/product/${product._id}`}>{product.Title}</Link></span></h5>
            <h5><span className='single-product-price'>{formatCurrency(product.Price)}</span></h5>
            <h6 className='stock-view'>{(product.CountInStock > 0) ? (<span class="badge badge-success">In Stock</span>) : (<span class="badge badge-danger">Out Of Stock</span>)}</h6>
            </div>
                
        </div>
        )
    
}